
export const rootUrl = 'https://api.connectu.ml/api/';
export const authUrl = `${rootUrl}token/`;
export const coreUrl = `${rootUrl}core/`;
export const userUrl = `${coreUrl}user/`;
export const questionUrl = `${coreUrl}question/`;
export const upvoteUrl = `${coreUrl}upvote/`;
export const answerUrl = `${coreUrl}answer/`;
export const pairUrl = `${coreUrl}mentor_pair/`;
export const notificationUrl = `${coreUrl}notification/`;
export const feedbackUrl = `${coreUrl}feedback/`;
export const aboutMeUrl = `${coreUrl}about_me/`;



export function getHeaders() {
    let headers = {
        'Content-Type': 'application/json'
    }
    const token = localStorage.getItem('token');
    if (token !== undefined && token !== null) {
        headers['Authorization'] = `Token ${token}`;
    }

    return headers;
}


export async function jsonFetch({fetchPromise}) {
    const response = await fetchPromise;
    if (!response.ok) {
        throw Error('Request failed');
    }
    return await response.json();
}

export function isUserLoggedIn() {
    return localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined;
}
