import React, {Component} from "react";
import {
    Button, TextField,
    Typography
} from "@material-ui/core";
import {withStyles} from '@material-ui/core/styles';
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import profileIcon from "../../images/profile-photo-icon-red.svg";
import {createInterests, getUserFromStorage} from "../../services/user_api";

const useStyles = (theme) => ({
    heading: {
        fontWeight: 700,
        fontSize: 36
    },
    subheading: {
        fontWeight: 500
    },
    button: {
        width: '40%'
    },
    text: {
        fontWeight: 700
    },


});


export class Interests extends Component {

    constructor(props) {
        super(props);

        this.login = this.login.bind(this);
    }

    state = {
        answer_1: '',
        answer_2: '',
        answer_3: ''
    }

    async login() {
        await this.props.login();
        const user = getUserFromStorage();
        console.log(user);
        await createInterests({
            answer_1: this.state.answer_1,
            answer_2: this.state.answer_2,
            answer_3: this.state.answer_3,
            id: user.student.id
        });
    }


    render() {
        const classes = this.props;
        return (

            <div>

                <Typography variant="h3" color="primary" className={classes.heading}

                >
                    Signup
                </Typography>
                <br/>
                <Typography variant="body1" color="secondary" className={classes.subheading}>
                    We would like to know you better
                </Typography>
                <br/>

                <div style={{
                    width: '100%',
                    backgroundColor: 'white',
                    margin: 12,
                    fontFamily: 'Raleway'
                }}>
                    <div style={{display: 'flex', justifyContent: 'flex-start', alignContent: 'flex-end'}}>
                        <span>1. What are your dreams?</span>
                    </div>
                    <TextField
                        id="outlined-size-small"
                        label={<span
                        >Let your thoughts flow!</span>}
                        variant="outlined"
                        size="small"
                        fullWidth
                        margin="normal"

                        onChange={(event) => this.setState({answer_1: event.target.value})}
                    />

                    <div style={{display: 'flex', justifyContent: 'flex-start', alignContent: 'flex-end'}}>
                        <span>2. What do you like to do in free time?</span>
                    </div>
                    <TextField
                        id="outlined-size-small"
                        label={<span
                        >Hobbies, Interests!</span>}
                        variant="outlined"
                        size="small"
                        fullWidth
                        margin="normal"

                        onChange={(event) => this.setState({answer_2: event.target.value})}
                    />

                    <div style={{display: 'flex', justifyContent: 'flex-start', alignContent: 'flex-end'}}>
                        <span>3. Where do you see yourself in next 4 years?</span>
                    </div>
                    <TextField
                        id="outlined-size-small"
                        label={<span
                        >Out in the world...</span>}
                        variant="outlined"
                        size="small"
                        fullWidth
                        margin="normal"

                        onChange={(event) => this.setState({answer_3: event.target.value})}
                    />
                </div>


                <div style={
                    {display: 'flex', justifyContent: 'space-evenly'}
                }>
                    <Button variant="contained" size="large"
                            className={classes.button} onClick={() => this.props.handlePage({page: 'signup'})}>
                        <ArrowBackIcon/> <span style={{marginLeft: 5}}>Back</span>
                    </Button>

                    <Button variant="contained" color="primary" size="large"
                            className={classes.button} onClick={this.login}>
                        Signup
                    </Button>
                </div>
            </div>
        );
    }
}

export default withStyles(useStyles, {withTheme: true})(Interests);
